/**
 * 工位缺陷实时监控
 * Created by Administrator on 2016/12/28.
 */
var security = require("security");
var mqttLib = require("mqttLib");
var util = require('util');
var kit = require("kit");

var realTimeMonitoringOfPositionDefectCtrl = {

    init: function () {
        new vue({
            data: {
                workStationList:[],
                workTime: "0",// 时间0当天，1本周，2本月
                frequency:"1",// 频次1是大于1   /2是大于2/ 3是大于3  
                station: undefined,
                andonTypeCode: null,
                allAndons: null,
                dataTable: undefined
            },
            // 挂载对象
            el: ".realTimeTop",
            mounted: function () {
                
                $("[name=date]").change((e) => {
                    this.workTime = $(e.target).val();
                    this.dataTable.destroy();
                    this.loadPage();
                });

                $("[name=frequency]").change((e) => {
                    this.frequency = $(e.target).val();
                    this.dataTable.destroy();
                    this.loadPage();
                });

                $(".day").click(function() {
                    $("[name=date]")[0].click();
                });

                $(".week").click(function() {
                    $("[name=date]")[1].click();
                });

                $(".month").click(function() {
                    $("[name=date]")[2].click();
                });

                $(".frequency1").click(()=> {
                    $("[name=frequency]")[0].click();
                });

                $(".frequency2").click(()=> {
                    $("[name=frequency]")[1].click();
                });

                $(".frequency3").click(()=> {
                    $("[name=frequency]")[2].click();
                });

                util.setBreadcrumb(["工位缺陷实时监控"]);
                this._getWorkStation();
                // setInterval(()=> {
                //     this.getCount()
                // },1000)
            },
            methods:{
                getCount: function(response) {
                    util.service("GMES-CLIENT-PQC0008", {
                        serviceCode: "GMES-CLIENT-PQC0008",
                        statusECode: "1",
                        typeCode: "",
                        stationCodeArray: this.workStationList,
                        workTime: this.workTime,
                        frequency: this.frequency
                    })
                    .done((response) => {
                        // $(".day").html("当日（"+ response.data.totalCount1 +"）");
                        // $(".week").html("本周（"+ response.data.totalCount2 +"）");
                        // $(".month").html("本月（"+ response.data.totalCount3 +"）");
                    });

                },
                _getWorkStation: function () {
                    var arr ="";
                    
					kit.forEach(security.getUserOperableStations(), function(value, key) {

						arr += value.stationCode + ",";
                    });
                    
                    this.workStationList = arr;
					if (this.workStationList.length) {
                        this.loadPage();
                        this.subscribeMessageTopic();
                       
					} else {
						notie.alert({ text: "暂无可用工位" });
					}
				},
                // 主题监听
                subscribeMessageTopic: function () {

                    // 函数节流
                    var refresh = kit.throttle(() => {
                        // 随机数降高峰
                        setTimeout(() => {
                            this.dataTable.destroy();
                            this.loadPage();
                        },Math.random()*5*1000);

                    }, 60000);

                    var topic = "defectReport/PQC/" + security.getSiteCode() + "/DefectAcquisition/defective";
                    mqttLib.subscribe(topic);
                    mqttLib.registerMessageHandler(topic, "andonMonitoring.andonMonitoringMessageHandler", function (message) {
                        refresh();
                    });

                    // setInterval(()=> {
                    //     refresh()
                    // },2000);
                },
                loadPage: function () {

                    // this.getCount();
                    //表格加载
                    var scrollY = ($(".pqc_realTimeMonitoringOfPositionDefect").height()-220);
                    // $(".realTimeTable").height(scrollY )
                    // var scrollY = $(".realTimeTable").height() - 40;// (600 - 220 - 43 - 53);
                    // var scrollY;
                    // scrollY1 > scrollY2 ? scrollY = scrollY2 : scrollY = scrollY;
                    this.dataTable = util.dataTable($('.realTimeTable table', this.page), {

                        scrollY: scrollY,
                        "order": [],
                        "ajax": {
                            serviceCode: "GMES-CLIENT-PQC0008",
                            param: {
                                serviceCode: "GMES-CLIENT-PQC0008",
                                statusECode: "1",
                                typeCode: "",
                                stationCodeArray: this.workStationList,
                                workTime: this.workTime,
                                frequency: this.frequency
                            },
                        },
                        "columns": [
                            {
                                "data": "id",
                                "orderable": false,
                                "render": function (data, type, row, meta) {
                                    return meta["settings"]["_iDisplayStart"] + meta.row + 1
                                }
                            },
                            {
                                "data": "productNum",
                                "name": "productNum"
                            },
                            {
                                "data": "positionText",
                                "name": "positionText"
                            },
                            {
                                "data": "directionText",
                                "name": "directionText"
                            },
                            {
                                "data": "defectTypeText",
                                "name": "defectTypeText",
                                // "width": "55px",                     
                            },
                            {
                                "data": "responSibilityTypeText",
                                "name": "responSibilityTypeText",
                                "orderable": false
                            },
                            {
                                "data": "severityText",
                                "name": "severityText"
                            },
                            {
                                "data": "stationText",
                                "name": "stationText"
                            },
                            {
                                "data": "pqcdateTime",
                                "name": "pqcdateTime"
                            },
                            {
                                "data": "pqcuser",
                                "name": "pqcuser"
                            },
                            {
                                "data": "eCodeText",
                                "name": "eCodeText"
                            }
                        ]
                    },(response) => {
                        $(".day").html("当日（"+ response.data.totalCount1 +"）");
                        $(".week").html("本周（"+ response.data.totalCount2 +"）");
                        $(".month").html("本月（"+ response.data.totalCount3 +"）");
                    });
                },
            }
        });
    }
};

module.exports = realTimeMonitoringOfPositionDefectCtrl;


